<template>
  <div>
    <div class="card" style="width: 18rem;">
      <img class="card-img-top" :src="img" alt="Card image cap" />
      <div class="card-body">
        <h3 class="card-title">{{ this.facets }}</h3>
        <h5 class="card-title">{{ this.covid[0].Date }}</h5>
        <table class="table">
          <thead class="thead-dark">
            <tr>
              <th scope="col">{{ this.facets }}</th>
              <th scope="col">Country</th>
            </tr>
          </thead>
          <tbody v-if="this.facets == 'NewConfirmed'">
            <tr v-for="c in this.filterCountries" :key="c.Country">
              <td>{{ c.NewConfirmed }}</td>
              <td>{{ c.Country }}</td>
            </tr>
          </tbody>
          <tbody v-if="this.facets == 'NewDeaths'">
            <tr v-for="c in this.filterCountries" :key="c.Country">
              <td>{{ c.NewDeaths }}</td>
              <td>{{ c.Country }}</td>
            </tr>
          </tbody>
          <tbody v-if="this.facets == 'NewRecovered'">
            <tr v-for="c in this.filterCountries" :key="c.Country">
              <td>{{ c.NewRecovered }}</td>
              <td>{{ c.Country }}</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "covid",
  data() {
    return { covid: [] };
  },
  props: {
    facets: String,
    nCountries: Number
  },
  created() {
    this.getAllCases();
  },
  methods: {
    async getAllCases() {
      const response = await fetch("https://api.covid19api.com/summary");
      const myData = await response.json();
      this.covid = myData.Countries;
      console.log(this.covid);

      if (this.facets == "NewConfirmed") {
        this.covid.sort(this.compareCases);
        this.img = require("../assets/confirmed.png");
      }
      if (this.facets == "NewDeaths") {
        this.covid.sort(this.compareDeaths);
        this.img = require("../assets/deaths.jpg");
      }
      if (this.facets == "NewRecovered") {
        this.covid.sort(this.compareRecovered);
        this.img = require("../assets/recovered.jpg");
      }
    },
    compareCases(a, b) {
      if (a.NewConfirmed > b.NewConfirmed) return -1;
      if (a.NewConfirmed < b.NewConfirmed) return 1;
      else return 0;
    },
    compareDeaths(a, b) {
      if (a.NewDeaths > b.NewDeaths) return -1;
      if (a.NewDeaths < b.NewDeaths) return 1;
      else return 0;
    },
    compareRecovered(a, b) {
      if (a.NewRecovered > b.NewRecovered) return -1;
      if (a.NewRecovered < b.NewRecovered) return 1;
      else return 0;
    }
  },
  updated() {
    this.getAllCases();
  },
  computed: {
    filterCountries() {
      return this.covid.slice(0, this.nCountries);
    }
  }
};
</script>
